.card {
  border: 1px solid gray;
  border-radius: 8px;
  text-align: left;
  width: 300px;
  height: 230px;
  &-media {
    max-width: 300px;
    max-height: 140px;
    position: relative;
    &__image {
      width: 298px;
      height: 140px;
      border-radius: 8px;
      object-fit: cover;
    }
    &__heart {
      position: absolute;
      top: 5px;
      right: 5px;
      font-size: 25px;
      padding: 3px ;
      background-color: lightgray;
      color: white;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      cursor: pointer;
      transition: 0.1s ease-in-out;
      &:active{
        color: red;
      }
      &:hover{
        background: lightcoral;
      }

    }
  }
  &-detail {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    &__title {
      font-size: 18px;
      width: 200px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    &__desc {
      font-size: 20px;
      color: red;
      font-weight: bolder;
    }
  }
  &-subDetail {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    padding: 5px 10px;
    background: white;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    color: gray;
    &__rating {
      font-size: 12px;
    }
    &__btn {
      background: transparent;
      border: none;
      outline: none;
      cursor: pointer;
      font-size: 20px;
    }
  }
}
