.header {
  height: 70px;
  background: lightblue;
  &-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    &__logo {
      .link {
        font-size: 25px;
        text-decoration: none;
      }
    }
    &__navLinks {
        display: flex;
        align-items: center;
        gap: 30px;
      .navLink {
        font-size: 18px;
        text-decoration: none;
      }
    }
  }
}
