.form {
  max-width: 50%;
  margin: 20px auto;
  // border: 1px solid red;
  &-subContainer {
    margin: 15px 0;
    &__title {
      font-size: 23px;
      color: rgb(100, 49, 49);
    }
    &__input {
      display: block;
      font-size: 20px;
      width: 100%;
      padding: 6px 10px;
      outline: none;
      border: 1px solid lightgray;
      border-radius: 3px;
      cursor: pointer;
    }
    &__btn {
      padding: 8px 45px;
      font-size: 18px;
      font-weight: bold;
      border: 1px solid transparent;
      outline: none;
      border-radius: 4px;
      background: rgb(62, 120, 139);
      color: white;
      transition: 0.3s ease-in-out;
      cursor: pointer;
      &:hover {
        background: rgb(26, 79, 97);
        color: rgb(233, 233, 233);
      }
    }
  }
}
